.delivery-list-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    width: 100%;
  }
  
  .filters {
    width: 100%;
    max-width: 400px; /* Limita el ancho máximo similar a AddDelivery */
    display: flex;
    flex-direction: column;
    align-items: center; /* Centra todos los elementos dentro de la sección de filtros */
  }
  
  .filters div {
    margin-bottom: 15px;
    width: 100%; /* Hace que cada div ocupe el 100% del ancho */
  }
  
  label {
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  select, input {
    padding: 8px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 100%; /* Asegura que los inputs y selects ocupen todo el ancho disponible */
  }
  
  table {
    width: 100%;
    max-width: 800px;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  th, td {
    padding: 12px;
    border: 1px solid #ccc;
    text-align: left;
  }
  
  th {
    background-color: #f2f2f2;
  }

  .subtitle {
    font-family: "Fascinate Inline", system-ui;
    font-weight: 400;
    font-style: normal;
    font-size: 26px;
    color: #333;
    text-shadow: 2px 2px 4px #6EC5C3;
    margin-bottom: 35px;
    margin-top: 0px;
  }
  
  button {
    padding: 10px 20px;
    font-size: 16px;
    color: white;
    background-color: #007BFF;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-top: 10px; /* Añade un margen superior */
    width: 100%;
    max-width: 200px;
  }
  
  button:hover {
    background-color: #555;
  }
  
  select {
    padding: 8px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    width: 100%; /* Asegura que el select ocupe todo el ancho disponible */
  }
  
  /* Estilo para pantallas pequeñas */
  @media (max-width: 600px) {
    table, thead, tbody, th, td, tr {
      display: block;
    }
  
    tr {
      margin-bottom: 15px;
    }
  
    td {
      text-align: right;
      padding-left: 50%;
      position: relative;
    }
  
    td::before {
      content: attr(data-label);
      position: absolute;
      left: 0;
      width: 45%;
      padding-left: 15px;
      font-weight: bold;
      text-align: left;
    }
  
    th {
      display: none;
    }
  }
  
  .back-button-container {
    margin-top: 20px;
  }
  
  .back-button {
    padding: 10px 20px;
    font-size: 16px;
    color: white;
    background-color: #333;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .back-button:hover {
    background-color: #555;
  }
  